@import '../variables';

.about {
    position: relative;
    margin-top: 110px;
    min-height: 100vh;
    background: url(../../img/about.jpg) center center/cover no-repeat;
    transition: all .5s;
    scroll-snap-align: start;

    &__overlay {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 100vh;
        padding: 300px 5vw;
        background: rgba(0, 0, 0, .4);
    }

    &__title {
        position: relative;
        font-size: 40px;
        color: #fff;
        letter-spacing: 2px;
        margin-bottom: 40px;
        &::before {
            content: '';
            width: 5px;
            height: 100%;
            background-color: $main-color;
            border-radius: 5px;
            position: absolute;
            left: -20px;
        }
    }

    &__job {
        font-size: 36px;
        color: #fff;
        letter-spacing: 2px;
        margin-bottom: 20px;
        span {
            color: $main-color;
        }
    }

    &__descr {
        width: 37vw;
        font-size: 20px;
        color: #fff;
        letter-spacing: 2px;
        margin-bottom: 10px;
    }

    &__img {
        width: 500px;
        animation: guy 4s ease infinite;
    }
}

@keyframes guy {
    0% {
        transform: scale(1);
      }
      50% {
        transform: scale(1.1);
      }
      100% {
        transform: scale(1);
      }
}

@media (max-width: 1280px) {
    .about {
        &__overlay {
            padding: 200px 5vw;
        }

        &__title {
            font-size: 30px;
        }

        &__job {
            font-size: 26px;
        }

        &__descr {
            width: 40vw;
            font-size: 16px;
        }

        &__img {
            width: 300px;
        }
    }
}

@media (max-width: 768px) { 
    .about {
        margin-top: 0;

        &__overlay {
            justify-content: center;
            align-items: center;
        }

        div:first-child {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }

        &__title {
            margin-left: 20px;
        }

        &__job {
            margin-bottom: 0;
        }

        &__descr {
            width: 100%;
        }

        &__img {
            display: none;
        }
    }
}