@import '../variables';

.projects {
    position: relative;
    min-height: 100vh;
    padding: 130px 300px 80px 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    scroll-snap-align: start;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(../../img/about.jpg) center center/cover no-repeat;
        z-index: -1;
    }

    &__title {
        position: relative;
        font-size: 40px;
        color: #fff;
        letter-spacing: 2px;
        margin-bottom: 30px;
        &::before {
            content: '';
            width: 5px;
            height: 100%;
            background-color: $main-color;
            border-radius: 5px;
            position: absolute;
            left: -20px;
        }
    }

    &__container {
        display: flex;
        justify-content: center;
        gap: 30px;
        align-items: center;
        flex-wrap: wrap;

    }

    &__item {
        position: relative;
        width: 18vw;
        height: 200px;
        border: 1px solid white;
        border-radius: 30px;
        margin-top: 10px;
        overflow: hidden;
        transition: all .5s;
        &:hover {
            transform: scale(1.1);
            .projects__item_overlay {
                right: 0;
            }
        }

        &_mobile {
            position: absolute;
            top: 10px;
            right: 10px;
            svg {
                animation: mobile 3s ease infinite;
            }
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: -1;
        }

        &_overlay {
            width: 100%;
            height: 100%;
            padding: 20px 30px;
            position: absolute;
            top: 0;
            right: -100%;
            transition: all .5s;
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: rgba(0, 0, 0, .7);
                z-index: -1;
            }
        }
        
        &_title {
            font-size: 26px;
            font-weight: 900;
            letter-spacing: 2px;
            margin-bottom: 15px;
        }

        h4 {
            font-size: 20px;
            font-weight: 700;
            letter-spacing: 2px;
        }

        span {
            display: flex;
            gap: 5px;
            margin: 10px 0;

            img {
                width: 32px;
                height: 32px;
            }
        }

        button {
            width: 100px;
            height: 30px;
            border: 0;
            border-radius: 10px;
            box-shadow: -4px 4px 5px black;
            background: $main-color;
            color: #000;
            font-size: 18px;
            font-weight: 900;
            letter-spacing: 2px;
            transition: all .5s;
            cursor: pointer;
            &:hover {
                transform: scale(1.2);
                margin-top: 8px;
                margin-left: 10px;
                padding-top: 2px;
                padding-left: 10px;
                box-shadow: -6px -4px 5px black;
            }
        }


        a {
            width: auto;
            height: 30px;
            img {
                width: 24px;
                height: 24px;
                transition: all .8s;
            &:hover {
                width: 32px;
                height: 32px;
            }
            }
        }

    }

    &__linkWrapper {
        display: flex;
        gap: 30px;
    }
}

@keyframes mobile {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}

@media (min-width: 1281px) and (max-width: 1550px) {
    .projects {
        padding: 130px 19vw;

        &__item {
            width: 45%;
        }
    }
}

@media (max-width: 1280px) {
    .projects {
        padding: 150px 20px;
        &::before {
            background: url(../../img/phoneBG.jpg) center center/cover no-repeat;
        }

        &__title {
            font-size: 30px;
            margin-left: 20px;
        }

        &__container {
            flex-direction: column;
        }

        &__item {
            width: 50%;
        }
    }
}

@media (max-width: 768px) {
    .projects {
        &__item {
            width: 80%;

            &_title {
                font-size: 22px;
            }

            h4 {
                font-size: 16px;
            }

            &_overlay {
                padding: 5vw;
            }
        }
    }
}