@import '../variables';

.navbar {
    position: absolute;
    top: -120px;
    padding: 10px 60px;
    width: 100%;
    height: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #131214;
    z-index: 10;

    &__fixed {
        position: fixed;
        top: 0;
    }

    &__logo {
        font-size: 28px;
        font-weight: 900;
        text-align: center;
        color: $main-color;
    }

    &__name {
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 5px;
        color: #fff;
    }

    &__links {
        display: flex;
        align-items: flex-end;
        gap: 100px;
        height: 30px;
    }

    &__item {
        position: relative;
        display: inline-flex;
        font-size: 20px;
        letter-spacing: 2px;
        color: #fff;
        transition: all .8s;
        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0;
            height: 2px;
            background-color: #fff;
            transition: all .8s;
        }
        &:hover {
            color: $main-color;
            &:after {
                width: 100%;
                background-color: $main-color;
            }
        }
    }
}

@media (max-width: 1280px) {
    .navbar {
        padding: 10px 30px;

        &__logo {
            font-size: 24px;
        }

        &__name {
            font-size: 20px;
        }

        &__item {
            font-size: 16px;
        }
    }
}

@media (min-width: 769px) and (max-width: 965px) {
    .navbar {
        justify-content: center;
        &__logo, &__name {
            display: none;
        }

        &__item {
            font-size: 20px;
        }
    }
}

@media (max-width: 768px) { 
    .navbar {
        display: none;
    }   
}