@import '../variables';

.skills {
    position: relative;
    min-height: 100vh;
    padding: 200px 15vw;
    scroll-snap-align: start;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(../../img/about.jpg) center center/cover no-repeat;
        transform: scaleX(-1) rotate(180deg);
        z-index: -1;
    }

    &__title {
        position: relative;
        font-size: 40px;
        color: #fff;
        letter-spacing: 2px;
        margin-bottom: 40px;
        &::before {
            content: '';
            width: 5px;
            height: 100%;
            background-color: $main-color;
            border-radius: 5px;
            position: absolute;
            left: -20px;
        }
    }

    &__wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__container {
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 60px;

        ul {
            li {
                font-size: 30px;
                list-style: none;
                position: relative;
                padding: 0 0 0 20px;
                transition: all .5s;
                letter-spacing: 2px;
                margin-bottom: 20px;
                display: flex;
                align-items: center;
                gap: 20px;
                svg {
                    width: 0;
                    transition: all .5s;
                }
                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 13px;
                    height: 5px;
                    width: 5px;
                    border: 1px solid $main-color;
                    border-width: 2px 2px 0 0;
                    transform: rotate(45deg);
                    transition: all .5s;
                }
            }
        }
    }

    &__subtitle {
        font-size: 27px;
        letter-spacing: 2px;
    }

    &__html, &__css, &__js, &__git, &__gulp, &__react {
        position: absolute;
    }
    &__html {
        top: 284px;
        left: 3vw;
        transform: rotate(48deg);
    }
    &__css {
        top: 650px;
        left: 7vw;
        transform: rotate(30deg);
    }
    &__js {
        top: 730px;
        left: 27vw;
        transform: rotate(6deg);
    }
    &__git {
        top: 730px;
        left: 50vw;
        transform: rotate(-7deg);
    }
    &__gulp {
        top: 591px;
        left: 70vw;
        transform: rotate(-28deg);
    }
    &__react {
        top: 275px;
        left: 74vw;
        transform: rotate(-43deg);
    }
}

@media (min-width: 769px) {
    .skills {
        &__container {
            ul {
                li {
                    &:hover {
                        transform: scale(1.05);
                        &::before {
                            width: 10px;
                            height: 10px;
                            transform: rotate(225deg);
                        }
                        svg {
                            width: 32px;
                        }
                    }
                }
            }
        }
    }
}

@media (max-width: 1280px) {
    .skills {
        &__title {
            font-size: 30px;
        }

        &__subtitle {
            font-size: 22px;
        }

        &__container {
            ul {
                li {
                    font-size: 20px;
                }
            }
        }

        svg {
            width: 86px;
        }
        
    }
}

@media (max-width: 768px) {
    .skills {
        padding: 120px 20px;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            width: 64px;
        }

        &__wrapper {
            flex-direction: column;
            justify-content: center;
        }

        &__subtitle {
            margin-left: 20px;
            font-size: 30px;
            font-style: italic;
            text-decoration: underline;
        }

        &__container {
            width: 100%;
            ul {
                li {
                    font-size: 17px;
                    height: 20px;
                    &::before {
                        top: 6px;
                    }
                }
            }
        }

        &__html{
            left: 7vw;
            top: 135px;
        }
        &__css {
            top: 316px;
        }
        &__js {
            left: 7vw;
            top: 500px;
        }
        &__git {
            left: 72vw;
            top: 620px;
        }
        &__gulp {
            left: 72vw;
            top: 400px;
        }
        &__react {
            top: 200px;
            left: 72vw;
        }
        &__html, &__css, &__js, &__git, &__gulp, &__react {
            opacity: .4;
            filter: blur(2px);
            z-index: -1;
            transform: rotate(0);
        }
    }
}