@import '../variables'; 

.home {
    display: inline-flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    scroll-snap-align: start;

    h1, h2 {
        text-align: center;
        color: #fff;
    }

    &__welcome {
        font-size: 34px;
        letter-spacing: 5px;
    }

    &__title {
        font-size: 60px;
        font-weight: 900;
        text-transform: uppercase;
        letter-spacing: 3px;
    }

    &__job {
        font-size: 36px;

        span {
            color: $main-color;
        }
    }
}

@media (min-width: 966px) and (max-width: 1280px) {
    .home {
        &__welcome {
            font-size: 30px;
        }

        &__title {
            font-size: 46px;
        }

        &__job {
            font-size: 32px;
        }
    }
}

@media (min-width: 769px) and (max-width: 965px) {
    .home {
        &__welcome {
            font-size: 22px;
        }

        &__title {
            font-size: 38px;
        }

        &__job {
            font-size: 24px;
        }
    }
}

@media (max-width: 768px) {
    .home {
        &__welcome {
            font-size: 28px;
        }

        &__title {
            font-size: 36px;
        }

        &__job {
            font-size: 30px;
        }
    }
}