@import '../variables';

.menu {
    width: 20%;
    height: 100vh;
    padding: 40px 0;
    display: inline-flex;
    flex-direction: column;
    gap: 200px;
    align-items: center;
    background-color: #131214;
    z-index: 10;

    &__logo {
        margin: 26px 0;
        font-size: 32px;
        font-weight: 900;
        text-align: center;
        color: $main-color;
    }

    &__name {
        font-size: 28px;
        font-weight: 700;
        letter-spacing: 5px;
        color: #fff;
    }

    &__links {
        width: 130px;

        h2, h3 {
            display: none;
        }
    }

    &__item {
        position: relative;
        display: inline-flex;
        margin-bottom: 30px;
        font-size: 20px;
        letter-spacing: 2px;
        color: #fff;
        transition: all .8s;
        &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0;
            height: 2px;
            background-color: #fff;
            transition: all .8s;
        }
        &:first-child {
            margin-top: 30px;
        }
    }

    &__follow {
        display: flex;
        flex-direction: column;
        gap: 20px;

        p {
            font-size: 18px;
            letter-spacing: 2px;
            color: white;
        }
    
        a {
            align-self: center;
            img {
                width: 24px;
                height: 24px;
                transition: all .8s;
            &:hover {
                width: 48px;
                height: 48px;
            }
            }
        }
    }

    &__hamburger {
        display: none;
    }
}

@media (min-width: 966px) and (max-width: 1280px) {
    .menu {
        width: 30%;
        padding: 30px 0;
        gap: 0;
        justify-content: space-between;

        &__logo {
            font-size: 24px;
        }

        &__name {
            font-size: 20px;
        }

        &__follow {
            p {
                font-size: 15px;
            }
        }
    }
}

@media (min-width: 769px) {
    .menu__item {
        &:hover {
            margin-left: 10px;
            color: $main-color;
            &:after {
                width: 100%;
                background-color: $main-color;
            }
        }
    }
}

@media (min-width: 769px) and (max-width: 965px) {
    .menu {
        width: 30%;
        padding: 30px 0;
        gap: 0;
        justify-content: space-between;

        &__logo {
            font-size: 26px;
        }

        &__name {
            font-size: 16px;
        }

        &__links {
            width: 105px;
        }

        &__item {
            font-size: 18px;
        }

        &__follow {
            p {
                font-size: 15px;
            }
        }
    }
}

@media (max-width: 768px) {
    .menu {
        position: fixed;
        width: 100%;
        height: 60px;
        padding: 10px 40px;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 0;

        &__logo {
            font-size: 26px;
        }

        &__name {
            display: none;
        }

        &__links {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100vh;
            padding: 0 40px;
            background: #131214;
            position: absolute;
            top: 0;
            left: -100%;
            flex-direction: column;
            gap: 30px;
            padding-bottom: 20px;
            overflow: hidden;
            transition: .8s all;

            h2 {
                display: block;
                margin: 0;
            }

            h3 {
                display: block;
                margin-bottom: 50px;
                text-align: center;
            }
        }

        &__item {
            font-size: 18px;
            &::before {
                background: #4b5563;
                bottom: -15px;
                content: "";
                display: block;
                height: 1px;
                left: 0;
                position: absolute;
                width: 100%;
            }
        }

        &__follow {
            display: none;
        }

        &__hamburger {
            display: block;

            span {
                background-color: #fff;
                display: block;
                height: 2px;
                margin: 0 auto 4px;
                width: 26px;    
                transition: all .5s;
            }
            &_active {
                span {
                    margin-bottom: -2px
                }
                span:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg);
                }
                span:nth-child(2) {
                    width: 0;
                    height: 0;
                }
                span:nth-child(3) {
                    transform: translateY(3px) rotate(45deg);
                }
            }
        }
    }

    .show {
        left: 0;
    }

    .overflow {
        overflow: hidden;
    }
}