* {
    text-decoration: none;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: #fff;
    scroll-behavior: smooth;
}

.container {
    width: 80%;
    height: 100vh;
    float: right;
    background: url(./img/laptop.jpg) center center/cover no-repeat;

    &__overlay {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .7);
        display: flex;
        align-items: center;
    }
}

.divider {
    width: 100%;
    height: 60px;
    border: 0;
    background-color: #131214;
}

.footer {
    width: 100%;
    height: 100px;
    background-color: #131214;
    padding-top: 15px;

    &__follow {
        text-align: center;
        display: flex;
        flex-direction: column;
        gap: 20px;

        p {
            font-size: 18px;
            letter-spacing: 2px;
            color: white;
        }
    
        a {
            align-self: center;
            img {
                width: 24px;
                height: 24px;
                transition: all .8s;
            &:hover {
                width: 32px;
                height: 32px;
            }
            }
        }
    }
}

@media (min-width: 769px) and (max-width: 1280px) {
    .container {
        width: 70%;
    }
}

@media (max-width: 768px) {
    .container {
        float: initial;
        width: 100%;
    }
}